.content {
    grid-area: content;
    padding-top: 10px;
    padding-right: 10px;
    margin-bottom: 10px;
  }
  
  .linkPost {
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    margin-bottom: 10px;
    padding-right: 25px;
    transition: color 0.36s ease-in-out;
  }
  
  .linkPost:after {
    content: '';
    position: absolute;
    inset: 0;
    border-radius: 20px;
    transition: box-shadow 0.36s ease-in-out;
  }
  
  .linkAuthor {
    position: relative;
    overflow: hidden;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    transition: color 0.36s ease-in-out;
    width: min-content;
  }
  
  .linkAuthor:hover {
    color: #2d2d2d;
  }

  @media (min-width: 580px) {
    .linkPost:hover {
      color: #cc6633;
    }
  
    .linkPost:hover:after {
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
    }
  }

  